const scroll = calcScroll();

function calcScroll() { 
    let div = document.createElement('div'); 

    div.style.width = '50px'; 
    div.style.height = '50px';
    div.style.overflowY = 'scroll'; 
    div.style.visibility = 'hidden'; 

    document.body.appendChild(div); 
    let scrollWidth = div.offsetWidth - div.clientWidth; 
    div.remove(); 

    return scrollWidth; 
}

function openModal(modalSelector, modalTimerId) { /* (эти две ф исп в этом модуле и в модуле forms, поэтому выносим их из ф и вконце скрипта экспортируем, ф в формах подключаем. Тоже подключаем с пом аргументов) */
    const modal = document.querySelector(modalSelector);
    modal.classList.add("show");
    modal.classList.remove("hide");
    document.body.style.overflow = "hidden";
    document.body.style.marginRight = `${scroll}px`;

    if (modalTimerId) {
        clearInterval(modalTimerId); 
    }
}

function closeModal(modalSelector) {
    const modal = document.querySelector(modalSelector); 
    modal.classList.add("hide");
    modal.classList.remove("show");
    document.body.style.overflow = "";
    document.body.style.marginRight = `0px`;
}

function modal(triggerSelector, modalSelector, modalTimerId) { /* (чтобы ф не была так привязана к постоянным п, запускать ее будем через аргументы, далее в скрипте меняем дата-модалы и селекторы класса на аргументы) */
    const modalTrigger = document.querySelectorAll(/* "[data-modal]" */triggerSelector);
    const modal = document.querySelector(/* ".modal" */modalSelector); 

    modalTrigger.forEach(btn => {
        btn.addEventListener("click", /* openModal */() => openModal(modalSelector, modalTimerId)/* (так как стрелочная ф не подключится с аргументом, прописываем такую структуру - оборачиваем ее в безымянную ф) */);
    });

    modal.addEventListener("click", (e) => {
        if (e.target === modal || e.target.getAttribute("data-close") == "") { 
            closeModal(modalSelector);   
        }
    });

    document.addEventListener("keydown", (e) => {
        if (e.code === "Escape" && modal.classList.contains("show")) {    
            closeModal(modalSelector);
        }
    });

    // const modalTimerId = setInterval(openModal, 50000); (эта п понадобится не только в этом модуле, но и в других, поэтому вырезаем ее и переносим в сборочный блок(script.js в д.с)) 

    function showModalByScroll() {
        if (window.pageYOffset + document.documentElement.clientHeight >= document.documentElement.scrollHeight) {    
            // openModal();  
            openModal(modalSelector, modalTimerId); 
            window.removeEventListener('scroll', showModalByScroll);  
        }
    }

    window.addEventListener("scroll", showModalByScroll); 
}

// module.exports = modal;
export default modal;
export {openModal, closeModal};